import resources from './resources'
import media from './media'
import finances from './finances'
import awarenessLoyalty from './awareness&loyalty'
import monthSales from './monthSales'
import sales from './sales'

Vue.use(Vuex)
Vue.use(VueAxios, axios)

export default new Vuex.Store({
	modules: {
		user, shared, promo, auth, games, team,
    segment, channels, advancedFeatures, products,
    promotion, resources, media, finances, awarenessLoyalty,
    monthSales, sales
	}
})
