import Vue from 'vue';
import http from '../utils/http';

export default {
    state: {
        spots: null,
        spots_all: null,
        promotions: null,
        promotions_all: null,
        //constants
        CORE_MESSAGES: ['design', 'material', 'manufacturing'],
        FEATURES: [
            { text: 'positioning tracker', value: 1 },
            { text: 'power bank', value: 2 },
            { text: 'missed items warning', value: 3 },
            { text: 'danger alarm', value: 4 },
            { text: 'fridge camera', value: 5 },
            { text: 'individual pictures', value: 6 },
            { text: 'against loss insurance', value: 7 },
            { text: 'environment friendly utilization', value: 8 },
            { text: 'charity profit sharing', value: 9 }
        ],
        PRICES: ['low', 'attractive', 'reasonable'],
        VALUES: [
            { text: 'Family / kids', value: 'family' },
            { text: 'Personal attractiveness', value: 'attractiveness' },
            { text: 'Strong personality', value: 'personality' },
            { text: 'Social status', value: 'social_status' },
            { text: 'Fun', value: 'fun' },
            { text: 'Friendship (relations)', value: 'friendship' },
            { text: 'Pets', value: 'pets' },
            { text: 'Independent lifestyle', value: 'independent' }, 
        ],
        CHANNELS: [
            'TV', 
            'visual outdoor ads',
            'autoradio podcasts', 
            'postal mailings',
            'SMM', 
            'content marketing', 
            'influencers'
        ],
        OBJECTIVES: ['create demand', 'stimulate purchase', 'maintain loyalty', 'introduce brand'],
    },
    mutations: {
        loadAllSpots(state, payload) {
			state.spots_all = payload;
		},
        loadSpots(state, payload) {
			state.spots = payload;
		},
        loadActions(state, payload) {
			state.promotions = payload;
		},
        loadTeamActions(state, payload) {
			state.promotions = payload;
		},
        addSpot(state, payload) {
            if (!state.spots_all) {
                state.spots_all = payload;
            } else {
                const index = state.spots_all.length
                Vue.set(state.spots_all, index, payload)
            }   
        },
        addTeamSpot(state, payload) {
            if (!state.spots) {
                state.spots = payload;
            } else {
                const index = state.spots.length;
                Vue.set(state.spots, index, payload);
            }  
        },
        addTeamPromotion(state, payload) {
            if (!state.promotions) {
                state.promotions = payload;
            } else {
                let index = state.promotions.length
                Vue.set(state.promotions, index, payload)
            }  
        },
        deleteSpot(state, payload) {
            state.spots =  state.spots.filter(item => item.id !== payload)
        },
        deleteAllSpot(state, payload) {
            state.spots_all =  state.spots_all?.filter(item => item.id !== payload);
        },
        deleteAction(state, payload) {
            state.promotions =  state.promotions?.filter(item => item.id !== payload)
        }
    },
    actions: {
        //fetch all spotts by game id & round
        async fetchSpots({commit}, payload) {
            commit('clearError')
			commit('setLoading', true)
            
            try {
                const result = await http.get(`/spot/game/${payload.gameId}?round=${payload.round}`);
                console.log(result)
                if (result) {
                   const spots = await result.data 
                   commit('loadAllSpots', spots)
                }              
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            }
        },
        async fetchTeamSpots({commit}, payload) {
            commit('clearError')
			commit('setLoading', true)
            
            try {
                const result = await http.get(`/spot/team/${payload.teamId}?gameId=${payload.gameId}&round=${payload.round}`);
                //console.log(result)
                if (result.status == 200) {
                   const spots = await result.data 
                   commit('loadSpots', spots)
                }              
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            }
        },
        async fetchPromotions({commit}, payload) {
            commit('clearError')
			commit('setLoading', true)
            
            try {
                const result = await http.get(`/promotion/game/${payload.gameId}?round=${payload.round}`);
                
                if (result && result.data) {
                   const actions = await result.data 
                   commit('loadActions', actions)
                }              
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            }
        },
        async fetchTeamPromotions({commit}, payload) {
            commit('clearError')
			commit('setLoading', true)
            
            try {
                const result = await http.get(`/promotion/team/${payload.teamId}?gameId=${payload.gameId}&round=${payload.round}`);
                
                if (result && result.data) {
                   const actions = await result.data 
                   commit('loadTeamActions', actions)
                }              
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            }
        },
        async addSpot({commit}, payload) {
            commit('clearError')
			commit('setLoading', true)

            const team = payload.team;
            delete payload.team;
            payload.teamId = team.id;

            const trademark = payload.trademark;
            delete payload.trademark;
            payload.trademarkId = trademark.id;

            try {
                const result = await http.post(`/spot`, payload);
                
                if (result) {
                   const spot = await result.data; 
                   spot.team = team;
                   spot.trademark = trademark;
                   commit('addSpot', spot);
                }                              
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            }
        },
        async addTeamSpot({commit}, payload) {
            commit('clearError')
			commit('setLoading', true)

            const team = payload.team;
            delete payload.team;
            payload.teamId = team.id;

            const trademark = payload.trademark;
            delete payload.trademark;
            payload.trademarkId = trademark.id;

            try {
                const result = await http.post(`/spot`, payload);
                
                if (result) {
                   const spot = await result.data; 
                   spot.team = team;
                   spot.trademark = trademark;
                   commit('addTeamSpot', spot);
                }                              
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            }
        },
        async addTeamPromotion({commit}, payload) {
            commit('clearError')
			commit('setLoading', true)

            const team = payload.team;
            delete payload.team;
            payload.teamId = team.id;

            const trademark = payload.trademark;
            delete payload.trademark;
            payload.trademarkId = trademark.id;

            try {
                const result = await http.post(`/promotion`, payload);
                
                if (result) {
                   const actions = await result.data; 
                   actions.team = team;
                   actions.trademark = trademark;
                   commit('addTeamPromotion', actions);
                }                              
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            }
        },
        async deleteSpot({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)

            try {
                await http.delete(`/spot/${payload}`);
                commit('deleteSpot', payload)            
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        async deleteAllSpot({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)

            try {
                await http.delete(`/spot/${payload}`);
                commit('deleteAllSpot', payload)            
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        async deleteAction({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)

            try {
                await http.delete(`/promotion/${payload}`);
                commit('deleteAction', payload)            
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        async spotsTransition({commit}, payload) {
            commit('clearError')
			commit('setLoading', true)
            
            try {
                await http.get(`/spot/transition/${payload.gameId}?round=${payload.round}`);            
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            }
        },
    },
    getters: {
        spots (state) {
            return state.spots; //team
        },
        promotions (state) {
            return state.promotions;
        },
        promotions_all (state) {
            return state.promotions_all;
        },
        spots_all (state) {
            return state.spots_all;
        },
        CORE_MESSAGES (state) {
            return state.CORE_MESSAGES;
        },
        FEATURES (state) {
            return state.FEATURES;
        },
        PRICES (state) {
            return state.PRICES;
        },
        VALUES (state) {
            return state.VALUES;
        },
        CHANNELS (state) {
            return state.CHANNELS;
        },
        OBJECTIVES (state) {
            return state.OBJECTIVES;
        }
    }
}