import http from '../utils/http';
import Vue from 'vue';

export default {
    state: {
        users: []
    },
    mutations: {
        loadAll(state, payload) {
            state.users=payload;
        },
        addUser(state, payload) {
            const index = state.users.length
            Vue.set(state.users, index, payload) 
        }
    },
    actions: {
        async fetchAllUsers({commit}) {
            commit('clearError')
			commit('setLoading', true)

            try {
                const result = await http.get(`/user`);  
                if (result) {
                   commit('loadAll', result.data)
                }               
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            }
        },
        //creating the user account by professor
        async addUser({commit}, payload) {
            commit('clearError')
			commit('setLoading', true)

            try {
                const result = await http.post(`/auth/createUser`, payload);  
                if (result) {
                   commit('addUser', result.data)
                }               
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            } 
        }
    },
    getters: {
        users (state) {
            return state.users;
        }
    }
}