import Vue from 'vue'
import http from '../utils/http';

export default {
    state: {
        products: null,
        products_all: [],
        trademarks: [],
        DESIGN_TYPES: ['classic', 'business', 'casual', 'innovative', 'art'],
    },
    mutations: {
        loadAllProducts(state, payload) {
			state.products_all = payload
		},
        loadProducts(state, payload) {
			state.products = payload
		},
        loadTrademarks(state, payload) {
			state.trademarks = payload
		},
        addProduct(state, payload) {
            let index = state.products?.length || 0;
			Vue.set(state.products, index, payload)   
        },
        editProduct(state, payload) {
            const prod = state.products?.find(s => s.id == payload.id)
            let index = state.products?.indexOf(prod);
            if (!prod) {
                state.products = payload;
            } else {
                Vue.set(state.products, index, {...prod, ...payload })  
            }   
        },
        deleteProduct(state, payload) {
            state.products =  state.products?.filter(s => s.id !== payload)
        }
    },
    actions: {
        //fetch all products by game id & round
        async fetchProducts({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)
            //console.log(payload)
            try {
                const result = await http.get(`/product/game/${payload.gameId}?round=${payload.round}`);
                //console.log(result)
                if (result) {
                   const prods = result.data 
                   commit('loadAllProducts', prods) //
                }
                               
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        async fetchTeamProducts({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)

            const teamId = payload.teamId;
            const gameId = payload.gameId;
            const round = payload.round;
            
            try {
                const result = await http.get(`/product/team/${teamId}?gameId=${gameId}&round=${round}`);
                
                if (result && result.data) {
                   const prods = result.data 
                   commit('loadProducts', prods) //
                }
                               
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        //trademarks
        async fetchTrademarks({ commit }) {
            commit('clearError')
            commit('setLoading', true)

            try {
                const result = await http.get(`/trademark`);
                //console.log(result)
                if (result) {
                   const trademarks = await result.data 
                   commit('loadTrademarks', trademarks)
                }
                               
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        async addProduct({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)

            let team = {};
            const trademark = { name: payload.trademark };

            if (payload.team) {
                team = payload.team;
                delete payload.team;
            }

            try {
                const result = await http.post(`/product`, payload);
                
                if (result) {
                    const prod = await result.data;
                    prod.team = team;
                    prod.trademark = trademark;
                    commit('addProduct', prod)
                }              
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        async editProduct({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)
            
            let team = {};
            let trademark = {};
            let model = '';

            if (payload.team) {
                team = payload.team;
                delete payload.team;
            }

            if (payload.trademark) {
                trademark = payload.trademark;
                delete payload.trademark;
            }

            if (payload.model) {
                model = payload.model;
                delete payload.model;
            }

            try {
                await http.put(`/product/${payload.id}`, payload);

                payload.team = team;
                payload.trademark = trademark;
                payload.model = model;
                
                commit('editProduct', payload)            
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        async deleteProduct({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)

            try {
                await http.delete(`/product/${payload}`);
                commit('deleteProduct', payload)            
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        async productsTransition({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)
            //console.log(payload)
            try {
                await http.get(`/product/transition/${payload.gameId}?round=${payload.round}`);             
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
    },
    getters: {
        products(state) {
            return state.products; //team products
        },
        products_all(state) {
            return state.products_all;
        },
        trademarks(state) {
            return state.trademarks;
        },
        DESIGN_TYPES(state) {
            return state.DESIGN_TYPES;
        }
    }
}