import Vue from 'vue'
import http from '../utils/http';

export default {
    state: {
        segments: [],
        market_segments: [],
    },
    mutations: {
        loadSegments(state, payload) {
			state.segments = payload
		},
        loadMarketSegments(state, payload) {
			state.market_segments = payload
		},
        addSegment(state, payload) {
           const index = state.market_segments.length
			Vue.set(state.market_segments, index, payload)   
        },
        editSegment(state, payload) {
            const seg = state.market_segments.find(s => s.id == payload.id)
            const index = state.market_segments.indexOf(seg)
			Vue.set(state.market_segments, index, payload)   
        },
        deleteSegment(state, payload) {
            state.segments =  state.segments.filter(s => s.id !== payload)
        }
    },
    actions: {
        async fetchSegments({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)
            //payload = segment.category == set.item
            try {
                const result = await http.get(`/segment/${ payload }`);
                //console.log(result)
                if (result) {
                   const segments = await result.data 
                   commit('loadSegments', segments)
                }
                               
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        async fetchMarketSegments({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)

            const game_id = payload.gameId;
            const round = payload.round;

            try {
                const result = await http.get(`/market-segment/${ game_id }?round=${round}`);
                //console.log(result)
                if (result) {
                   const segments = await result.data 
                   commit('loadMarketSegments', segments)
                }
                               
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        //adding segment + market-segment
        async addSegment({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)

            try {
                const result = await http.post(`/segment`, { 
                    name: payload.name,
                    category: payload.category, 
                    peak_size: payload.peak_size 
                });
                //console.log(result)
                if (result && result.data && result.data.id) {
                    delete payload.name;
                    delete payload.category;
                    delete payload.peak_size;

                    payload.segmentId = result.data.id;
                    
                    const res = await http.post(`/market-segment`, payload)
                    const segment = await res.data
                    segment.segment = result.data;
                    //console.log(segment)
                    commit('addSegment', segment)
                }                               
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        async editSegment({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)
            console.log(payload)

            let segment = {}

            if (payload.segment) {
                segment = payload.segment;
                delete payload.segment;
            }

            try {
                await http.put(`/market-segment/${payload.id}`, payload);
                
                payload.segment = segment;
                
                commit('editSegment', payload)            
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        //todo 
        async deleteSegment({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)

            try {
                const result = await http.delete(`/segment/${payload}`);
                console.log(result)
                commit('deleteSegment', payload)            
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        //calculate segment actual_size
        async calculateActualSize({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)

            const game_id = payload.gameId;
            const round = payload.round;
            console.log(payload)

            try {
                const result = await http.get(`/market-segment/actualSize/${ game_id }?round=${round}`);
                console.log(result)
                if (result) {
                   const segments = await result.data 
                   commit('loadMarketSegments', segments)
                }
                               
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        //next round transition by game id & round
        async segmentTransition({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)
    
            try {
                const result = await http.get(`/market-segment/transition/${payload.gameId}?round=${payload.round}`);
                if (result) {
                   const segments = await result.data 
                   commit('loadMarketSegments', segments)
                }                
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
    },
    getters: {
        segments(state) {
            return state.segments;
        },
        market_segments(state) {
            return state.market_segments;
        },
    }
}