import Vue from 'vue';
import http from '../utils/http';

export default {
    state: {
        media_costs: null,
        media_coverage: null,
    },
    mutations: {
        loadMediaCosts(state, payload) {
			state.media_costs = payload;
		},
        loadMediaCoverage(state, payload) {
			state.media_coverage = payload;
		},
        addMediaCosts(state, payload) {
            let index = state.media_costs.length
			Vue.set(state.media_costs, index, payload)   
        },
        addMediaCoverage(state, payload) {
            let index = state.media_coverage.length
			Vue.set(state.media_coverage, index, payload)   
        },
        deleteMediaCosts(state, payload) {
            state.media_costs =  state.media_costs.filter(item => item.id !== payload)
        },
        deleteMediaCoverage(state, payload) {
            state.media_coverage =  state.media_coverage.filter(item => item.id !== payload)
        }
    },
    actions: {
        //fetch all media_costs by game id & round
        async fetchMediaCosts({commit}, payload) {
            commit('clearError')
			commit('setLoading', true)
            
            try {
                const result = await http.get(`/media-costs/game/${payload.gameId}?round=${payload.round}`);
                if (result) {
                   const media_costs = await result.data 
                   commit('loadMediaCosts', media_costs)
                }              
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            }
        },
        //fetch media_coverage by game id & round
        async fetchMediaCoverage({commit}, payload) {
            commit('clearError')
			commit('setLoading', true)
            
            try {
                const result = await http.get(`/media-coverage/game/${payload.gameId}?round=${payload.round}`);
                
                if (result) {
                   const media_coverage = await result.data 
                   commit('loadMediaCoverage', media_coverage)
                }              
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            }
        },
        async addMediaCosts({commit}, payload) {
            commit('clearError')
			commit('setLoading', true)

            try {
                const result = await http.post(`/media-costs`, payload);
                
                if (result) {
                   const media_costs = await result.data; 
                   commit('addMediaCosts', media_costs);
                }                              
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            }
        },
        async addMediaCoverage({commit}, payload) {
            commit('clearError')
			commit('setLoading', true)

            let segment = '';
            if (payload.segment) {
                segment = payload.segment
                delete payload.segment
            }

            try {
                const result = await http.post(`/media-coverage`, payload);
                
                if (result) {
                   const media_coverage = await result.data;
                   media_coverage.segment = segment
                   
                   commit('addMediaCoverage', media_coverage);
                }                              
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            }
        },
        async deleteMediaCosts({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)

            try {
                await http.delete(`/media-costs/${payload}`);
                commit('deleteMediaCosts', payload)            
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        async deleteMediaCoverage({ commit }, payload) {
            commit('clearError')
            commit('setLoading', true)

            try {
                await http.delete(`/media-coverage/${payload}`);
                commit('deleteMediaCoverage', payload)            
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
                commit('setLoading', false)
                throw error
            } 
        },
        async mediaCostsTransition({commit}, payload) {
            commit('clearError')
			commit('setLoading', true)
            
            try {
                await http.get(`/media-costs/transition/${payload.gameId}?round=${payload.round}`);          
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            }
        },
        async mediaCoverageTransition({commit}, payload) {
            commit('clearError')
			commit('setLoading', true)
            
            try {
                await http.get(`/media-coverage/transition/${payload.gameId}?round=${payload.round}`);     
                commit('setLoading', false)
            }
            catch(error) {
                commit('setError', error.message)
				commit('setLoading', false)
				throw error
            }
        },
    },
    getters: {
        media_costs (state) {
            return state.media_costs;
        },
        media_coverage (state) {
            return state.media_coverage;
        }

    }
}